import Timeline, {
  TimelineMarkers,
  CustomMarker,
  TodayMarker,
  CursorMarker
} from 'react-calendar-timeline';
// make sure you include the timeline stylesheet or the timeline will not be styled
import 'react-calendar-timeline/lib/Timeline.css';
import moment from 'moment';
import './App.css';

function App() {

  const groups = [
    { 
      id: 1, 
      title: 'group 1',
      // rightTitle: 'title in the right sidebar',
      // stackItems: true,
      // height: 100
    }, 
    { 
      id: 2, 
      title: 'group 2' 
    },
    { 
      id: 3, 
      title: 'group 3' 
    }
  ]

  const items = [
    {
      id: 1,
      group: 1,
      title: 'item 1',
      start_time: new Date('April 06, 2022 03:24:00'),
      end_time: new Date('April 06, 2022 06:24:00'),
      // The below three attributes can be added to the main timeline element, but we can override here
      canMove: false,
      canResize: false,
      canChangeGroup: false,
      itemProps: {
        // these optional attributes are passed to the root <div /> of each item as <div {...itemProps} />
        'data-custom-attribute': 'Random content',
        'aria-hidden': true,
        onDoubleClick: (id) => { alert(`You clicked double! at ${id}`) },
        className: 'weekend',
        style: {
          background: 'red'
        }
      }
    },
    {
      id: 2,
      group: 2,
      title: 'item 2',
      start_time: moment().add(-0.5, 'hour'),
      end_time: moment().add(2, 'hour')
    },
    {
      id: 3,
      group: 3,
      title: 'item 3',
      start_time: moment().add(2, 'hour'),
      end_time: moment().add(3, 'hour')
    }
  ]

  // The preferred (fastest) option is to give Unix timestamps in milliseconds for start_time and end_time. 
  // Objects that convert to them (JavaScript Date or moment()) will also work, but will be a lot slower.

  return (
    <div className="App">
      <div className='container-fluid'>
        <Timeline
          groups={groups}
          items={items}
          // visibleTimeStart={new Date('April 03, 2022 03:30:00')} 
          // visibleTimeEnd={new Date('April 15, 2022 04:40:00')}
          defaultTimeStart={moment().add(-12, 'hour')}
          defaultTimeEnd={moment().add(12, 'hour')}

          //###defaultTimeStart and defaultTimeEnd###
          //Unless overridden by visibleTimeStart and visibleTimeEnd, specify where the calendar begins and where it ends.
          //This parameter expects a Date or moment object.
          //###visibleTimeStart and visibleTimeEnd###
          //The exact viewport of the calendar. When these are specified, scrolling in the calendar must be orchestrated by the 
          //onTimeChange function. This parameter expects a Unix timestamp in milliseconds.
          //Note that you need to provide either defaultTimeStart/End or visibleTimeStart/End for the timeline to function

          // sidebarWidth= "0"
          //Width of the sidebar in pixels. If set to 0, the sidebar is not rendered. Defaults to 150.

          //sidebarContent
          //Everything passed here will be displayed above the left sidebar. Use this to display small filters or so. Defaults to null.

          //rightSidebarWidth
          //Width of the right sidebar in pixels. If set to 0, the right sidebar is not rendered. Defaults to 0.

          //rightSidebarContent

          //dragSnap="30"
          //Snapping unit when dragging items. Defaults to 15 * 60 * 1000 or 15min. When so, the items will snap to 15min intervals when dragging.

          lineHeight= {80}
          //Height of one line in the calendar in pixels. Default 30

          itemHeightRatio={0.5}
          //What percentage of the height of the line is taken by the item? Default 0.65

          minZoom= {24 * 60 * 60 * 1000}
          //Smallest time the calendar can zoom to in milliseconds. Default 60 * 60 * 1000 (1 hour)

          //maxZoom
          //Largest time the calendar can zoom to in milliseconds. Default 5 * 365.24 * 86400 * 1000 (5 years)

          //clickTolerance
          //How many pixels we can drag the background for it to be counted as a click on the background. Default 3

          canMove ={false}
          // Can items be dragged around? Can be overridden in the items array. Defaults to true

          canChangeGroup = {false}
          //Can items be moved between groups? Can be overridden in the items array. Defaults to true

          canResize = "both"
          //Can items be resized? Can be overridden in the items array. Accepted values: false, "left", "right", "both". Defaults to "right".
          // If you pass true, it will be treated as "right" to not break compatibility with versions 0.9 and below.

          // useResizeHandle = {true}
          //Append a special .rct-drag-right handle to the elements and only resize if dragged from there. Defaults to false

          // stackItems
          // Stack items under each other, so there is no visual overlap when times collide. Can be overridden in the groups array. Defaults to false. 
          // Requires millisecond or Moment timestamps, not native JavaScript Date objects.

          // traditionalZoom = {true}
          //Zoom in when scrolling the mouse up/down. Defaults to false

          //itemTouchSendsClick
          //Normally tapping (touching) an item selects it. If this is set to true, a tap will have the same effect, 
          //as selecting with the first click and then clicking again to open and send the onItemClick event. Defaults to false.

          timeSteps = {{
            second: 0,
            minute: 15,
            hour: 1,
            day: 1,
            month: 1,
            year: 1
          }}
          //With what step to display different units. E.g. 15 for minute means only minutes 0, 15, 30 and 45 will be shown.

          //scrollRef
          //Ref callback that gets a DOM reference to the scroll body element. Can be useful to programmatically scroll.

          onItemClick = {(itemId, e, time) => {console.log(itemId, time)}}
          //Called when an item is clicked. Note: the item must be selected before it's clicked... except if it's a touch event and itemTouchSendsClick is enabled. 
          //time is the time that corresponds to where you click on the item in the timeline.

          onItemSelect = {(itemId, e, time) => {console.log(itemId, time)}}
          //Called when an item is selected. This is sent on the first click on an item.
          //time is the time that corresponds to where you click/select on the item in the timeline.

          //onItemDeselect(e)
          //Called when deselecting an item. Used to clear controlled selected prop.

          // onCanvasClick(groupId, time, e)
          // Called when an empty spot on the canvas was clicked. Get the group ID and the time as arguments. For example open a "new item" window after this.

          // onCanvasDoubleClick(groupId, time, e)
          // Called when an empty spot on the canvas was double clicked. Get the group ID and the time as arguments.
        >
          <TimelineMarkers>
            {/* <TodayMarker /> */}
            {/* <CustomMarker date={Date.now()} /> */}
            <CustomMarker date={Date.now()}>
              {/* custom renderer for this marker */}
              {({ styles, date }) => {
                const customStyles = {
                  ...styles,
                  backgroundColor: 'deeppink',
                  width: '20px'
                }
                return <div style={customStyles} />
              }}
            </CustomMarker>
            <CursorMarker />
          </TimelineMarkers>
        </Timeline>
      </div>
    </div>
  );
}

export default App;
